import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getFiles, downloadFile } from '../../../actions/downloadActions';
import DownloadCard from './DownloadCard';
import Spinner from '../../common/Spinner';

class QuestionBank extends Component {
  componentDidMount() {
    this.props.getFiles();
  }
  onClickDownload = e => {
    e.persist();
    console.log(e.target.value);
    const requestBody = {
      fileName: e.target.value
    }
    this.props.downloadFile(requestBody);
  }
  render() {
    const { files } = this.props.file;
    let downloadableFiles = [];
    if ( files === null || files === undefined || files.classNotes === undefined || files.questions === undefined) {
      downloadableFiles = <Spinner />;
    } else {
      downloadableFiles = files.questions.map(file => (
        <DownloadCard key={file._id} onBtnClick={this.onClickDownload} file={file} file1={file.author} file2={file.details} />
      ));
    }
    return (
      <div className='masterhead-background'>
        <div className='mx-auto text-center persian-direction'><p className='text-white welcomePTag'>بانک سوالات</p></div>
        <div className='admin-scroll-y'>
          {downloadableFiles}
        </div>
      </div>
    )
  }
}
QuestionBank.propTypes = {
  getFiles: PropTypes.func.isRequired,
  downloadFile: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  file: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  file: state.file,
  auth: state.auth
});

export default connect(mapStateToProps, { getFiles, downloadFile })(QuestionBank);