import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { myApplications, deleteApplication } from '../../../actions/applicationActions';
import ApplicationCard from './ApplicationCard';

class MyApplications extends Component {
  componentDidMount() {
    this.props.myApplications();
  }
  onClickDelete = e => {
    e.persist();
    const deleteData = {
      applicationId: e.target.value, 
      userId: this.props.auth.user.id
    }
    this.props.deleteApplication(deleteData)
  }
  render() {
    const {applications} = this.props.application
    let apps = [];
    let youMeanNothing = false;
    const nothingHere = (
      <div className='notFoundPHeight blackBack'>
        <p className='notFoundPTag text-center font2 persian-direction'>تا کنون هیچ درخاستی از طرف شما ثبت نشده است.</p>
      </div>
    );
    if ( applications !== null || applications !== undefined || applications.usersApplications !== undefined) {
      if(applications.usersApplications !== undefined) {
        apps = applications.usersApplications.map(app => (
          <ApplicationCard key={app._id} id={app._id} subject={app.subject} onBtnClick={this.onClickDelete} type={app.type} details={app.details}  />
        ));
          if(applications.usersApplications.length === 0)
            youMeanNothing = true;
        }
      }
    return (
      <div className='masterhead-background'>
        <div className='mx-auto text-center persian-direction'><p className='welcomePTag text-white'>درخواست های من</p></div>
        <div className='admin-scroll-y'>
        {youMeanNothing ? nothingHere : apps}
        </div>
      </div>
    )
  }
}
MyApplications.propTypes = {
  myApplications: PropTypes.func.isRequired,
  deleteApplication: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  file: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  file: state.file,
  auth: state.auth,
  application: state.application
});

export default connect(mapStateToProps, { myApplications, deleteApplication })(MyApplications);