import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { deleteApplication } from '../../actions/applicationActions';
import { allApplicationsAdmin } from '../../actions/adminActions';
import ApplicationCard from './ApplicationCard';

class AllApps extends Component {
  componentDidMount() {
    this.props.allApplicationsAdmin();
  }
  onClickDelete = e => {
    e.persist();
    const deleteData = {
      applicationId: e.target.value, 
      userId: this.props.auth.user.id
    }
    this.props.deleteApplication(deleteData)
  }
  render() {
    const {admin} = this.props
    let apps = [];
    if ( admin.applications !== undefined && admin.applications.length!== undefined) {
      console.log(admin.applications)
      for (let index = 0; index < admin.applications.length; index++) {
        apps.push(
          (
            <ApplicationCard key={admin.applications[index]._id} id={admin.applications[index]._id} subject={admin.applications[index].subject} onBtnClick={this.onClickDelete} type={admin.applications[index].type} details={admin.applications[index].details} applicantEmail={admin.applications[index].applicantEmail} applicantID={admin.applications[index].applicantID} applicantName={admin.applications[index].applicantName} />
          )
        )
        
      }
    }
    return (
      <div className='masterhead-background'>
        <div className='mx-auto text-center persian-direction'><p className='welcomePTag'>hi</p></div>
        <div className='admin-scroll-y'>
          {apps}
        </div>
      </div>
    )
  }
}
AllApps.propTypes = {
  allApplicationsAdmin: PropTypes.func.isRequired,
  deleteApplication: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  file: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  file: state.file,
  auth: state.auth,
  admin: state.admin
});

export default connect(mapStateToProps, { allApplicationsAdmin, deleteApplication })(AllApps);