import { GET_FILES, FILES_LOADING, ADD_FILE, DOWNLOAD_FILE } from '../actions/types';


const initialState = {
  files: null,
  loading: false
}
export default function(state = initialState, action) {
  switch(action.type) {
    case FILES_LOADING:{
      return {
        ...state,
        loading: true
      }
    }
    case GET_FILES: {
      return {
        ...state,
        files: action.payload,
        loading: false
      }
    }
    case DOWNLOAD_FILE: {
      return {
        ...state,
        courses: action.payload,
        loading: false
      }
    }
    case ADD_FILE: {
      return {
        ...state
      };
    }
    default:
      return state;
  }
}