import React, { Component } from 'react';

class DashCard extends Component {
  render() {
    const { course, enrolled, onBtnClick } = this.props;
    const enrollBtn = (
      <button value={course._id} onClick={onBtnClick} className= "btn font1 mybtn1">‍‍‍‍‍‍ثبت نام</button>
    )
    const delteBtn = (
      <button value={course._id} onClick={onBtnClick} className="btn font1 mybtn1">ثبت نام با موفقیت انجام شده است</button>
    )
    const onceCourse = (
      <div style={{margin:'5px'}}>
        <p className="card-text">زمان برگزاری دوره در تارخ {course.courseYear}/{course.courseMonth}/{course.courseDay} خواهد بود</p>
      </div>
    )
    const periodicCourse = (
      <div style={{margin:'5px'}}>
        <p className="card-text">.زمان برگزاری کلاس آنلاین  {course.courseDayInWeek} و تعداد جلسات {course.NumberOfSessions} جلسه می باشد</p>
      </div>
    )

    return (
      <div className="card text-center admin-card margin-top-admin">
        <div className="card-header">
          {course.courseName}
        </div>
        <div className="card-body">
          <h5 className="card-title"> این دوره ی آموزشی توسط {course.courseTeacher} تدریس می شود</h5>
          {course.periodic? periodicCourse: onceCourse}
          <p>{course.courseDetails}</p>
          {enrolled? delteBtn: enrollBtn }
        </div>
        <div className="card-footer">
          .در حال حاضر {course.students.length+10} دانش آموز در این دوره ی آموزشی ثبت نام کردند
        </div>
      </div>
    )
  }
}

export default DashCard