import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import store from '../Store';

import jwt_decode from 'jwt-decode';
import setAuthToken from '../utils/setAuthToken';
import { setCurrentUser, logoutUser } from '../actions/authActions';

import PrivateRoute from '../Components/common/PrivateRoute';
import AdminRoute from '../Components/common/AdminRoute';

import '../Components/style/App.css';

// import '../Components/style/style.css';
import Landing from '../Components/layout/Landing';
import Navbar from '../Components/layout/Navbar';
// import Footer from '../Components/layout/Footer';
import Login from '../Components/auth/Login';
import Register from '../Components/auth/Register';
import UnderC from '../Components/layout/UnderC';
import NotFound from '../Components/layout/NotFound';

import Dashboard from '../Components/dashboard/Dashboard';

import MyClasses from '../Components/dashboard/Classes/MyClasses';
import AllClasses from '../Components/dashboard/Classes/AllClasses';

import ClassNotes from '../Components/dashboard/DownloadableSources/ClassNotes';
import QuestionBank from '../Components/dashboard/DownloadableSources/QuestionBank';

import ClassApplication from '../Components/dashboard/ClassApplication/ClassApplication';
import MyApplications from '../Components/dashboard/ClassApplication/MyApplications';

import AddCourse from '../Components/admin/AddCourse';
import AddFile from '../Components/admin/AddFile';
import AllUsers from '../Components/admin/AllUsers';
import AllCourses from '../Components/admin/AllCourses';
import AllApps from '../Components/admin/AllApps'

// Check for token
if (localStorage.jwtToken) { 
  // Set auth token header auth
  setAuthToken(localStorage.jwtToken);
  // Decode token and get user info and exp
  const decoded = jwt_decode(localStorage.jwtToken);
  // Set user and isAuthenticated
  store.dispatch(setCurrentUser(decoded));
  // Check for expired token
  const currentTime = Date.now() / 1000;
  if (decoded.exp < currentTime) {
    // Logout user
    store.dispatch(logoutUser());
    // TODO: Clear current Profile

    // Redirect to login
    window.location.href = '/login';
  }
}

class App extends Component {
  render() {
    return (
      <Provider store = {store} >
      <Router>
        <div>
          <Navbar />
          <Switch>
            <Route exact path="/" component={ Landing } />
            <Route path="/login" component={ Login } />
            <Route path="/register" component={ Register } />
            <Route path="/underconstruction" component={ UnderC } />

            <PrivateRoute path="/dashboard" component={ Dashboard } />

            <PrivateRoute path="/myclasses" component={ MyClasses } />
            <PrivateRoute path="/allclasses" component={ AllClasses } />

            <PrivateRoute path="/QuestionsBank" component={ QuestionBank } />
            <PrivateRoute path="/classnotes" component={ ClassNotes } />

            <PrivateRoute path="/classapplication" component={ ClassApplication } />
            <PrivateRoute path="/myapplications" component={ MyApplications } />

            <AdminRoute path="/addcourse" component={ AddCourse } />
            <AdminRoute path="/allusers" component={ AllUsers } />
            <AdminRoute path="/addfile" component={ AddFile } />
            <AdminRoute path="/allcourses" component={ AllCourses } />
            <AdminRoute path="/allapps" component={ AllApps } />

            <Route component={NotFound} />
          </Switch>
        </div>
      </Router>
      </Provider>
    );
  }
}

export default App;