import React, { Component } from 'react';
import PropTypes from 'prop-types';
//redux
import { connect } from 'react-redux';
import { allCourses } from '../../actions/adminActions';
class AllCourses extends Component {
  componentDidMount() {
    this.props.allCourses();
  }
  render() {
    const { courses } = this.props.admin;
    let coursesList = [];
    if (courses !== null && courses !== undefined && courses.length !== undefined ) {
      if (courses.length > 0) {
        coursesList = courses.map(course => (
        <div className="card text-center admin-card margin-top-admin">
          <div className="card-header">
            <h1>classCode: {course.classCode}</h1>
          </div>
          <div className="card-body">
            <h5 className="card-title">courseName: {course.courseName}</h5>
            <h5 className="card-title">periodic: {course.periodic ? 'ture' : 'false'}</h5>
            <h5 className="card-title">NumberOfSessions: {course.NumberOfSessions}</h5>
            <h5 className="card-title">courseDay: {course.courseDay}</h5>
            <h5 className="card-title">courseMonth: {course.courseMonth}</h5>
            <h5 className="card-title">courseYear: {course.courseYear}</h5>
            <h5 className="card-title">courseDetails: {course.courseDetails}</h5>
            <h5 className="card-title">courseTeacher: {course.courseTeacher}</h5>
            <h5 className="card-title">type: {course.type}</h5>
          </div>
        </div>
        ));
      }
    }
    return (
      <div className='masterhead-background'>
        {/* <h1 className='text-white text-center'>Admin</h1> */}
        <div className='admin-scroll-y'>{coursesList}</div>
      </div>
    )
  }
}

AllCourses.propTypes = {
  allCourses: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  admin: state.admin,
});

export default connect(mapStateToProps, { allCourses })(AllCourses);
