import axios from 'axios';
import { APPLY, DELETE_APPLICATION, GET_MY_APPLICATIONS, GET_ERRORS } from './types'; 

export const apply = (applicationData) => dispatch => {
  axios
    .post('api/classapplications/apply', applicationData)
    .then(res =>
      dispatch({
        type: APPLY,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: {}
      })
    );
};

export const deleteApplication = (deleteAppData) => dispatch => {
  axios
    .post('api/classapplications/deleteapplication', deleteAppData)
    .then(res =>
      dispatch({
        type: DELETE_APPLICATION,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: {}
      })
    );
};

export const myApplications = () => dispatch => {
  axios
    .get('api/classapplications/myapplications')
    .then(res =>
      dispatch({
        type: GET_MY_APPLICATIONS,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: {}
      })
    );
};