import React, { Component } from 'react';

class DownloadCard extends Component {
  render() {
    const { file, onBtnClick, file1, file2 } = this.props;
    const downloadBtn = (
      <button value={file.fileName} onClick={onBtnClick} className= "btn font1 mybtn1">دانلود</button>
    )
    return (
      <div className="card text-center admin-card margin-top-admin">
        <div className="card-header">
          {file.name}
        </div>
        <div className="card-body">
          <h5 className="card-title">{file1}</h5>
          <p>{file2}</p>
          <br />
          {downloadBtn}
        </div>
      </div>
    )
  }
}

export default DownloadCard;