import React, { Component } from 'react';
import PropTypes from 'prop-types';
//redux
import { connect } from 'react-redux';
import { addToCourses } from '../../actions/coursesActions';

class AddCourse extends Component {
  constructor() {
    super();
    this.state = {
      courseName: '',
      courseTeacher: '',
      courseDetails: '',
      courseYear: '',
      courseMonth: '',
      courseDay: '',
      courseDayInWeek: '',
      periodic: '',
      NumberOfSessions: '',
      type: '',
      classCode: '',
    }
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
  onChange = e => this.setState({ [e.target.name]: e.target.value });
  onSubmit = e => {
    e.preventDefault();
    const courseData = {
      courseName: this.state.courseName,
      courseTeacher: this.state.courseTeacher,
      courseDetails: this.state.courseDetails,
      courseYear: this.state.courseYear,
      courseMonth: this.state.courseMonth,
      courseDay: this.state.courseDay,
      courseDayInWeek: this.state.courseDayInWeek,
      periodic: this.state.periodic,
      NumberOfSessions: this.state.NumberOfSessions,
      type: this.state.type,
      classCode: this.state.classCode
    }
    this.props.addToCourses(courseData);
    this.props.history.push('/dashboard');
  };
  render() {
    return (
      <div className="masthead">
        <div className="container d-flex h-100 align-items-center">
          <div className="mx-auto text-center">
            <form onSubmit={this.onSubmit} method="post" id="myform">
              <h2>AddCourse</h2>

              <div className="input-group input-group-lg">
                <input 
                  type="classCode"
                  className='inputLayout inputBorder form-control'
                  name="classCode"
                  value={this.state.classCode}
                  onChange={this.onChange}
                  placeholder="classCode"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="courseName"
                  className='inputLayout inputBorder form-control'
                  name="courseName"
                  value={this.state.courseName}
                  onChange={this.onChange}
                  placeholder="courseName"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="courseTeacher"
                  className='inputLayout inputBorder form-control'
                  name="courseTeacher"
                  value={this.state.courseTeacher}
                  onChange={this.onChange}
                  placeholder="courseTeacher"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="courseDetails"
                  className='inputLayout inputBorder form-control'
                  name="courseDetails"
                  value={this.state.courseDetails}
                  onChange={this.onChange}
                  placeholder="courseDetails"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="courseYear"
                  className='inputLayout inputBorder form-control'
                  name="courseYear"
                  value={this.state.courseYear}
                  onChange={this.onChange}
                  placeholder="courseYear"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="courseMonth"
                  className='inputLayout inputBorder form-control'
                  name="courseMonth"
                  value={this.state.courseMonth}
                  onChange={this.onChange}
                  placeholder="courseMonth"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="courseDay"
                  className='inputLayout inputBorder form-control'
                  name="courseDay"
                  value={this.state.courseDay}
                  onChange={this.onChange}
                  placeholder="courseDay"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="courseDayInWeek"
                  className='inputLayout inputBorder form-control'
                  name="courseDayInWeek"
                  value={this.state.courseDayInWeek}
                  onChange={this.onChange}
                  placeholder="courseDayInWeek"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="periodic"
                  className='inputLayout inputBorder form-control'
                  name="periodic"
                  value={this.state.periodic}
                  onChange={this.onChange}
                  placeholder="periodic BOOLIAN (0,1)"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="NumberOfSessions"
                  className='inputLayout inputBorder form-control'
                  name="NumberOfSessions"
                  value={this.state.NumberOfSessions}
                  onChange={this.onChange}
                  placeholder="NumberOfSessions"
                />
              </div>

              <div className="input-group">
                <select name="type" 
                className= 'inputLayout inputBorder form-control'
                value={this.state.type} onChange={this.onChange}>
                    <option className="option">Type</option>
                    <option className="option" value='Public'>Public</option>
                    <option className="option" value='Semi-Private'>semi-Private</option>
                    <option className="option" value='Tutor'>Tutor</option>
                    <option className="option" value='ProblemSolving'>Problem Solving</option>
                </select>
              </div>

              <div className="form-row-last">
                <input type="submit" name="register" className="btn font1 mybtn1 submitLayout" value="Add Course" />
              </div>
            </form>
          </div>
        </div>
      </div>
    )
  }
}

AddCourse.propTypes = {
  addToCourses: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(mapStateToProps, { addToCourses })(AddCourse);