import axios from 'axios';
import { GET_ALL_USERS, GET_ERRORS, GET_ALL_COURSES, GET_ALL_APPLICAIONS } from './types'; 

export const allUsers = () => dispatch => {
  axios
    .get('/api/users/all')
    .then(res =>
      dispatch({
        type: GET_ALL_USERS,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: {}
      })
    );
};

