import axios from 'axios';
import { GET_FILES, FILES_LOADING, ADD_FILE } from './types';

export const getFiles = () => dispatch => {
  dispatch(setFilesLoading());
  axios
    .get('/api/downloads/all')
    .then(res =>
      dispatch({
        type: GET_FILES,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_FILES,
        payload: {}
      })
    );
};

export const setFilesLoading = () => {
  return {
    type: FILES_LOADING
  };
};

export const downloadFile = (requestBody) => dispatch => {
  const method = 'POST';
  const url = '/api/downloads/download';
  axios
    .request({
      url,
      method,
      responseType: 'blob',
      data: requestBody,
    })
    .then(({ data }) => {
      const downloadUrl = window.URL.createObjectURL(new Blob([data]));
      const link = document.createElement('a');
      link.href = downloadUrl;
      link.setAttribute('download', 'a.pdf'); //any other extension
      document.body.appendChild(link);
      link.click();
      link.remove();
    });
};

export const addToFiles = (fileData, history) => dispatch => {
  axios
    .post('api/downloads/addfile', fileData)
    .then(res => history.push('/dashboard'))
    .catch(err =>
      dispatch({
        type: ADD_FILE,
        payload: err
      })
    );
};
