import React, { Component } from 'react';

class DashCard extends Component {
  render() {
    const { onBtnClick, subject, type, details, id, applicantEmail, applicantID, applicantName } = this.props;
    const deleteBtn = (
      <button value={id} onClick={onBtnClick} className= "btn font1 mybtn1 max-w1">حذف درخواست</button>
    )

    return (
      <div className="card text-center admin-card margin-top-admin">
        <div className="card-header">
          subject: {subject}
        </div>
        <div className="card-body">
          <p>tyop: {type}</p>
          <p>details: {details}</p>
          <p>applicantEmail: {applicantEmail}</p>
          <p>applicantID: {applicantID}</p>
          <p>applicantName: {applicantName}</p>
          {deleteBtn}
        </div>
      </div>
    )
  }
}

export default DashCard