import axios from 'axios';
import { GET_COURSES, COURSES_LOADING, DELETE_COURSE, ENROLL_COURSE, ADD_COURSE } from './types';

export const getCourses = () => dispatch => {
  dispatch(setCoursesLoading());
  axios
    .get('/api/users/mycourse')
    .then(res =>
      dispatch({
        type: GET_COURSES,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_COURSES,
        payload: {}
      })
    );
};

export const setCoursesLoading = () => {
  return {
    type: COURSES_LOADING
  };
};

export const enrollCourse = (courseId) => dispatch => {
  // console.log(courseId);
  axios
    .post('/api/users/addcourse', courseId)
    .then(res =>
      dispatch({
        type: ENROLL_COURSE,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_COURSES,
        payload: {}
      })
    );
};

export const deleteCourse = (courseId) => dispatch => {
  axios
    .post('/api/users/removecourse', courseId)
    .then(res =>
      dispatch({
        type: DELETE_COURSE,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_COURSES,
        payload: {}
      })
    );
};

export const addToCourses = (courseData, history) => dispatch => {
  axios
    .post('api/courses/addcourse', courseData)
    .then(res => history.push('/dashboard'))
    .catch(err =>
      dispatch({
        type: ADD_COURSE,
        payload: err.response.data
      })
    );
};