import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getCourses, deleteCourse, enrollCourse } from '../../../actions/coursesActions';
import DashCard from './DashCard';
import Spinner from '../../common/Spinner';

class AllClasses extends Component {
  componentDidMount() {
    this.props.getCourses();
  }
  updateDashboardPage() {
    this.props.getCourses();
  }
  onClickEnroll = e => {
    e.persist();
    const requestBody = {
      courseId: e.target.value
    }
    this.props.enrollCourse(requestBody, this.props.history);
    window.setTimeout(window.location.reload(), 1000);
    // this.updateDashboardPage()
  }
  onClickRemove = e => {
    e.persist();
    const requestBody = {
      courseId: e.target.value
    }
    this.props.deleteCourse(requestBody, this.props.history);
    window.setTimeout(window.location.reload(), 1000);
    // this.updateDashboardPage()
  }
  render() {
    const { courses, loading } = this.props.course;
    let usersCourseItems = [];
    let otherCourseItems = [];
    if ( courses === null || courses === undefined || courses.usersCourses === undefined || loading) {
      usersCourseItems = <Spinner />;
    } else {
      if (courses.usersCourses.length > 0) {
        usersCourseItems = courses.usersCourses.map(course => (
          <DashCard key={course._id} onBtnClick={this.onClickRemove} enrolled={true} course={course} />
        ));
      }
    }
    if (courses === null || courses === undefined || courses.usersCourses === undefined || loading) {
      usersCourseItems = <Spinner />;
    } else {
      if (Object.keys(courses.otherCourses).length > 0) {
        otherCourseItems = courses.otherCourses.map(course => (
          <DashCard key={course._id} onBtnClick={this.onClickEnroll} enrolled={false} course={course} />
        ));
      }
    }
    return (
      <div className='masterhead-background'>
        <div className='mx-auto text-center persian-direction'><p className='welcomePTag text-white'>تمامی دوره های آموزشی</p></div>
        <div className='admin-scroll-y'>
          {usersCourseItems}
          {otherCourseItems}
        </div>
      </div>
    );
  }
}

AllClasses.propTypes = {
  getCourses: PropTypes.func.isRequired,
  enrollCourse: PropTypes.func.isRequired,
  deleteCourse: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  course: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  course: state.course,
  auth: state.auth
});

export default connect(mapStateToProps, { getCourses, deleteCourse, enrollCourse })(AllClasses);
