import React, { Component } from 'react';
import classnames from 'classnames';
import {withRouter} from 'react-router-dom';
import {Link} from 'react-router-dom';

//redux
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { registerUser } from '../../actions/authActions';

class Register extends Component {
  constructor() {
    super();
    this.state = {
      name: '',
      email: '',
      state: '',
      password: '',
      password2: '',
      phoneNumber: '',
      homeNumber: '',
      school: '',
      grade: '',
      field: '',
      errors: {}
    }
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    
    if(nextProps.errors) {
      this.setState({errors: nextProps.errors})
    }
    console.log(this.state.errors);
  }
  componentDidMount() {
    if (this.props.auth.isAuthenticated) {
      this.props.history.push('/dashboard');
    }
  }
  onChange = e => this.setState({ [e.target.name]: e.target.value });
  onSubmit = e => {
    e.preventDefault();
    const newUser = {
      name: this.state.name,
      email: this.state.email,
      phoneNumber: this.state.phoneNumber,
      homeNumber: this.state.homeNumber,
      password: this.state.password,
      password2: this.state.password2,
      school: this.state.school,
      state: this.state.state,
      grade: this.state.grade,
      field: this.state.field
    }
    this.props.registerUser(newUser, this.props.history);
  };
  render() {
    const { errors } = this.state;
    return (
      <div className="masthead">
        <div className=" admin-scroll-y scroll-padding container d-flex h-100 align-items-center">
          <div className="mx-auto text-center">
              <form onSubmit={this.onSubmit} method="post" id="myform">
                
                <div className="input-group input-group">
                  <input 
                    type="text"
                    name="name"
                    className= {classnames( 'inputLayout inputBorder form-control', { 'redBorder': errors.name})}
                    value={this.state.name}
                    onChange={this.onChange}
                    placeholder="نام و نام خانوادگی"
                  />
                  {errors.name && (
                  <div className='errorbackgroung persian-direction'>{errors.name}</div>)}
                </div>

                <div className="input-group input-group">
                  <input 
                    type="email"
                    name="email"
                    className= {classnames( 'inputLayout inputBorder form-control', { 'redBorder': errors.email})}
                    value={this.state.email}
                    onChange={this.onChange}
                    placeholder="ایمیل"
                  />
                  {errors.email && (
                  <div className='errorbackgroung persian-direction'>{errors.email}</div>)}
                </div>

                <div className="input-group input-group">
                  <input 
                    type="text"
                    name="state"
                    className= {classnames( 'inputLayout inputBorder form-control', { 'redBorder': errors.state})}
                    value={this.state.state}
                    onChange={this.onChange}
                    placeholder="استان محل سکونت"
                  />
                  {errors.state && (
                  <div className='errorbackgroung persian-direction'>{errors.state}</div>)}
                </div>

                <div className="input-group input-group">
                  <input 
                    type="number"
                    name="phoneNumber"
                    className= {classnames( 'inputLayout inputBorder form-control', { 'redBorder': errors.phoneNumber})}
                    value={this.state.phoneNumber}
                    onChange={this.onChange}
                    placeholder="شماره تلفن همراه"
                  />
                  {errors.phoneNumber && (
                  <div className='errorbackgroung persian-direction'>{errors.phoneNumber}</div>)}
                </div>

                <div className="input-group input-group">
                  <input 
                    type="number"
                    name="homeNumber"
                    className= {classnames( 'inputLayout inputBorder form-control', { 'redBorder': errors.homeNumber})}
                    value={this.state.homeNumber}
                    onChange={this.onChange}
                    placeholder="شماره تلفن منزل"
                  />
                  {errors.homeNumber && (
                  <div className='errorbackgroung persian-direction'>{errors.homeNumber}</div>)}
                </div>

                <div className="input-group input-group">
                  <input
                    type="password"
                    name="password"
                    className= {classnames( 'inputLayout inputBorder form-control', { 'redBorder': errors.password})}
                    value={this.state.password}
                    onChange={this.onChange}
                    placeholder="رمز عبور"
                  />
                  {errors.password && (
                  <div className='errorbackgroung persian-direction'>{errors.password}</div>)}
                </div>

                <div className="input-group input-group">
                  <input
                    type="password"
                    name="password2"
                    className= {classnames( 'inputLayout inputBorder form-control', { 'redBorder': errors.password2})}
                    value={this.state.password2}
                    onChange={this.onChange}
                    placeholder="تکرار رمز عبور"
                  />
                  {errors.password2 && (
                  <div className='errorbackgroung persian-direction'>{errors.password2}</div>)}
              </div>

              <div className="input-group input-group persian-direction">
                <select name="grade" 
                className= {classnames( 'inputLayout inputBorder form-control', { 'redBorder': errors.grade})} 
                value={this.state.grade} onChange={this.onChange}>
                    <option className="option">پایه تحصیلی</option>
                    <option className="option" value='11th'>پایه دهم</option>
                    <option className="option" value='11th'>پایه یازدهم</option>
                    <option className="option" value='12th'>پایه دوازدهم</option>
                    <option className="option" value='graduated'>فارق التحصیل</option>
                </select>
                {errors.grade && (
                <div className='errorbackgroung persian-direction'>{errors.grade}</div>)}
              </div>

              <div className="input-group input-group persian-direction">
                <select name="field" 
                className= {classnames( 'inputLayout inputBorder form-control', { 'redBorder': errors.field})} 
                value={this.state.field} onChange={this.onChange}>
                    <option className="option">رشته تحصیلی</option>
                    <option className="option" value="ریاضی">ریاضی</option>
                    <option className="option" value="تجربی">تجربی</option>
                </select>
                {errors.field && (
                <div className='errorbackgroung persian-direction'>{errors.field}</div>)}
              </div>

              <div className="input-group input-group">
                <input 
                  type="text"
                  name="school"
                  className= {classnames( 'inputLayout inputBorder form-control', { 'redBorder': errors.school})}
                  value={this.state.school}
                  onChange={this.onChange}
                  placeholder="نام مدرسه"
                />
                {errors.school && (
                <div className='errorbackgroung persian-direction'>{errors.school}</div>)}
              </div>

              <div className="form-row-last">
                <input type="submit" name="register" className="btn font1 mybtn1 submitLayout" value="ثبت نام" />
              </div>
            </form>
          </div>
        </div>
      <div>
      <nav className="footerCss">
        <div className="container marginP">
          <div className="marginP font1"><Link to='/underconstruction'>درباره روژانو</Link></div>
        </div>
      </nav>
      </div>
    </div>
    )
  }
}

Register.propTypes = {
  registerUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(mapStateToProps, { registerUser })(withRouter(Register));