import React, { Component } from 'react';
import PropTypes from 'prop-types';
//redux
import { connect } from 'react-redux';
import { addToFiles } from '../../actions/downloadActions';

class AddFile extends Component {
  constructor() {
    super();
    this.state = {
      fileName: '',
      author: '',
      field: '',
      details: '',
      name: '',
      type: ''
    }
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
  onChange = e => this.setState({ [e.target.name]: e.target.value });
  onSubmit = e => {
    e.preventDefault();
    const fileData = {
      fileName: this.state.fileName,
      author: this.state.author,
      field: this.state.field,
      details: this.state.details,
      name: this.state.name,
      type: this.state.type
    }
    this.props.addToFiles(fileData);
    this.props.history.push('/dashboard');
  };
  render() {
    return (
      <div className="masthead">
        <div className="container d-flex h-100 align-items-center">
          <div className="mx-auto text-center">
            <form onSubmit={this.onSubmit} method="post" id="myform">
              <h2>AddFile</h2>

              <div className="input-group input-group-lg">
                <input 
                  type="fileName"
                  className='inputLayout inputBorder form-control'
                  name="fileName"
                  value={this.state.fileName}
                  onChange={this.onChange}
                  placeholder="fileName"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="name"
                  className='inputLayout inputBorder form-control'
                  name="name"
                  value={this.state.name}
                  onChange={this.onChange}
                  placeholder="name"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="author"
                  className='inputLayout inputBorder form-control'
                  name="author"
                  value={this.state.author}
                  onChange={this.onChange}
                  placeholder="author"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="details"
                  className='inputLayout inputBorder form-control'
                  name="details"
                  value={this.state.details}
                  onChange={this.onChange}
                  placeholder="details"
                />
              </div>

              <div className="input-group input-group-lg">
                <input 
                  type="field"
                  className='inputLayout inputBorder form-control'
                  name="field"
                  value={this.state.field}
                  onChange={this.onChange}
                  placeholder="field"
                />
              </div>

              <div className="input-group">
                <select name="type" 
                className= 'inputLayout inputBorder form-control'
                value={this.state.grade} onChange={this.onChange}>
                    <option className="option">Type</option>
                    <option className="option" value='classNotes'>Class Notes</option>
                    <option className="questions" value='Tutor'>questions</option>
                </select>
              </div>

              <div className="form-row-last">
                <input type="submit" name="register" className="btn font1 mybtn1 submitLayout" value="Add File" />
              </div>
            </form>
          </div>
        </div>
      </div>
    )
  }
}

AddFile.propTypes = {
  addToFiles: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(mapStateToProps, { addToFiles })(AddFile);