import { GET_COURSES, COURSES_LOADING, DELETE_COURSE, ENROLL_COURSE, ADD_COURSE } from '../actions/types';


const initialState = {
  courses: null,
  loading: false
}
export default function(state = initialState, action) {
  switch(action.type) {
    case COURSES_LOADING:{
      return {
        ...state,
        loading: true
      }
    }
    case GET_COURSES: {
      return {
        ...state,
        courses: action.payload,
        loading: false
      }
    }
    case DELETE_COURSE: {
      return {
        ...state,
        courses: action.payload,
        loading: false
      }
    }
    case ENROLL_COURSE: {
      return {
        ...state,
        courses: action.payload,
        loading: false
      }
    }
    case ADD_COURSE: {
      return {
        ...state
      };
    }
    default:
      return state;
  }
}