import { GET_ALL_USERS, GET_ALL_COURSES, GET_ALL_APPLICAIONS } from '../actions/types'; 

const initialState = {
  users: {},
  applications: {},
  courses: {}
}
export default function(state = initialState, action) {
  switch(action.type) {
    case GET_ALL_USERS:
      // console.log(action.payload);
      return {
        ...state,
        users: action.payload
      };
    case GET_ALL_APPLICAIONS:
      // console.log(action.payload);
      return {
        ...state,
        applications: action.payload
      };
    case GET_ALL_COURSES:
      // console.log(action.payload);
      return {
        ...state,
        courses: action.payload
      };
    default:
      return state;
  }
}