import { APPLY, DELETE_APPLICATION, GET_MY_APPLICATIONS } from '../actions/types'; 

const initialState = {
  applications: {}
}
export default function(state = initialState, action) {
  switch(action.type) {
    case APPLY:
      return {
        ...state,
        applications: action.payload
      };
    case DELETE_APPLICATION:
      return {
        ...state,
        applications: action.payload
      };
    case GET_MY_APPLICATIONS:
      return {
        ...state,
        applications: action.payload
      };
    default:
      return state;
  }
}