import React, { Component } from 'react';
import PropTypes from 'prop-types';
//redux
import { connect } from 'react-redux';
import { apply } from '../../../actions/applicationActions';

class ClassApplication extends Component {
  constructor() {
    super();
    this.state = {
      subject: '',
      type: '',
      details: '',
    }
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
  onChange = e => this.setState({ [e.target.name]: e.target.value });
  onSubmit = e => {
    e.preventDefault();
    const application = {
      subject: this.state.subject,
      type: this.state.type,
      details: this.state.details,
      applicantName: this.props.auth.user.name,
      applicantID: this.props.auth.user.id,
      applicantEmail: this.props.auth.user.email,
    }
    // this.props.apply(application);
    this.props.apply(application);
    this.props.history.push('/dashboard');
  };
  render() {
    return (
      <div className="masthead">
        <div className="container d-flex h-100 align-items-center">
          <div className="mx-auto text-center">
            <form onSubmit={this.onSubmit} method="post" id="myform">
              <h2>ثبت درخواست کلاس</h2>

              <div className="input-group input-group-lg">
                <input 
                  type="subject"
                  className='inputLayout inputBorder form-control'
                  name="subject"
                  value={this.state.subject}
                  onChange={this.onChange}
                  placeholder="موضوع کلاس"
                />
              </div>

              <div className="input-group">
                <select name="type" 
                className= 'inputLayout inputBorder form-control'
                value={this.state.grade} onChange={this.onChange}>
                    <option className="option">نوع کلاس</option>
                    <option className="option" value='Public'>کلاس گروهی</option>
                    <option className="option" value='Semi-Private'>کلاس نیمه خصوصی</option>
                    <option className="option" value='Tutor'>کلاس خصوصی</option>
                    <option className="option" value='ProblemSolving'>کلاس رفع اشکال</option>
                </select>
              </div>


              <div className="input-group input-group-lg">
                <input 
                  type="details"
                  className='inputLayout inputBorder form-control'
                  name="details"
                  value={this.state.details}
                  onChange={this.onChange}
                  placeholder="توضیحات"
                />
              </div>

              <div className="form-row-last">
                <input type="submit" name="register" className="btn font1 mybtn1 submitLayout" value="ارسال درخواست" />
              </div>
            </form>
          </div>
        </div>
      </div>
    )
  }
}

ClassApplication.propTypes = {
  apply: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(mapStateToProps, { apply })(ClassApplication);